import neilModal from '@/components/neil-modal/neil-modal.vue';
import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
import empty from "@/components/empty";
export default {

	components: {
		uniLoadMore,
		empty,
		neilModal
	},
	filters: {
		priceFormat(price) {
			return price / 100.0
		},
		// dateFormat(time) {
		// 	// return 'temp'
		// 	return formatDate(new Date(time),'yyyy-MM-dd HH:mm')
		// }
	},
	data() {
		return {
			statusMap: {
				0: '未付款',
				1: '正在拼团',
				2: '待出库',
				3: '待收货',
				4: '待评价',
				5: '已完成',
				6: '退款中',
				7: '已退款',
				8: '已取消',
				9: '已取消(系统)'
			},
			submiting: false,
			refundShow: false,
			inputRefundReason: '',
			refundOrderItem: '',
			tabCurrentIndex: 0,
			navList: [{
					state: '',
					text: '全部',
					loadingType: 'more',
					pageNo: 1,
					orderList: []
				},
				{
					state: 0,
					text: '待付款',
					loadingType: 'more',
					pageNo: 1,
					orderList: []
				},
				{
					state: 2,
					text: '待出库',
					loadingType: 'more',
					pageNo: 1,
					orderList: []
				},
				{
					state: 3,
					text: '待收货',
					loadingType: 'more',
					pageNo: 1,
					orderList: []
				},
				{
					state: 4,
					text: '待评价',
					loadingType: 'more',
					pageNo: 1,
					orderList: []
				},
				{
					state: 6,
					text: '退款中',
					loadingType: 'more',
					pageNo: 1,
					orderList: []
				}
			],
		};
	},
	onLoad(options) {
		/**
		 * 修复app端点击除全部订单外的按钮进入时不加载数据的问题
		 * 替换onLoad下代码即可
		 */
		this.tabCurrentIndex = 0
		for (let i = 0; i < this.navList.length; i++) {
			if (this.navList[i].state === parseInt(options.state)) {
				this.tabCurrentIndex = i
			}
		}




		if (options.state == 0) {
			this.loadData()
		}


	},

	methods: {
		//获取订单列表
		loadData(source) {
			const that = this
			//这里是将订单挂载到tab列表下
			let index = that.tabCurrentIndex;
			let navItem = that.navList[index];
			let state = navItem.state;

			if (source === 'tabChange' && navItem.loaded === true) {
				//tab切换只有第一次需要加载数据
				return;
			}
			if (navItem.loadingType === 'loading') {
				//防止重复加载
				return;
			}

			navItem.loadingType = 'loading';

			let orderList = that.$api.request('order', 'getOrderPage', {
				pageNo: navItem.pageNo,
				status: navItem.state
			}).then(res => {
				navItem.pageNo = res.data.pageNo + 1
				navItem.loadingType = res.data.pageNo < res.data.totalPageNo ? 'more' : 'noMore'
				res.data.items.forEach(item => {
					navItem.orderList.push(item);
					item.skuCount = 0
					item.skuList.forEach(skuItem => {
						item.skuCount += skuItem.num
					})
				})
				//loaded新字段用于表示数据加载完毕，如果为空可以显示空白页
				that.$set(navItem, 'loaded', true);
			})
		},

		//swiper 切换
		changeTab(e) {
			this.tabCurrentIndex = e.target.current;
			this.loadData('tabChange');
		},
		//顶部tab点击
		tabClick(index) {
			this.tabCurrentIndex = index;
		},
		payOrder(item) {
			uni.navigateTo({
				url: '/pages/pay/pay?orderno='+ item.orderNo + '&price=' + item.actualPrice
			})
		},
		//取消订单
		cancelOrder(item) {
			const that = this
			uni.showModal({
				title: '取消？',
				content: '您确定要取消此订单吗？',
				success : (e) => {
					if (e.confirm) {
						that.submiting = true
						that.$api.request('order', 'cancel', {
							orderNo: item.orderNo
						}, failres => {
							that.submiting = false
							that.$api.msg(failres.errmsg)
						}).then(res => {
							that.submiting = false
							item.status = 80
						})
					}
				}
			})
			
		},
		//订单退款
		refundConfirm() {
			const that = this
			if (that.submiting) {
				return
			}
			that.submiting = true
			that.$api.request('order', 'refund', {
				orderNo: that.refundOrderItem.orderNo,
				reason: that.inputRefundReason
			}, failres => {
				that.submiting = false
				that.$api.msg(failres.errmsg)
			}).then(res => {
				that.submiting = false
				that.refundOrderItem.status = 60
				that.$api.msg('申请退款成功！')
			})
		},
		refundOrder(item) {
			const that = this
			that.refundOrderItem = item
			that.refundShow = true
			that.inputRefundReason = ''
		},
		//确认订单
		confirmOrder(item) {
			const that = this
			uni.showModal({
				title: '收货？',
				content: '您确定要确认收货吗？',
				success : (e) => {
					if (e.confirm) {
						that.submiting = true
						that.$api.request('order', 'confirm', {
							orderNo: item.orderNo
						}, failres => {
							that.submiting = false
							that.$api.msg(failres.errmsg)
						}).then(res => {
							that.submiting = false
							item.status = 40
						})
					}
				}
			})
		},
		showShipTrace(item) {
			uni.navigateTo({
				url: "/pages/order/trace?orderno=" + item.orderNo
			})
		},
		//评价订单
		appraiseOrder(item) {
			uni.navigateTo({
				url: '/pages/order/appraise?orderid=' + item.id
			})
		}
	}
}
